@font-face {
    font-family: 'NunitoSans 7pt Condensed Black';
    src: url('../fonts/NunitoSans_7pt_Condensed-Black.ttf') format('truetype');
    font-weight: bold; /* Adjust font weight if necessary */
  }
  
  .slogan-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10; /* Ensure the slogan is above other elements */
  }
  
  .slogan-border {
    padding: 40px 80px; /* Increased padding */
    border: 4px solid rgba(0, 0, 0, 0.5); /* Slightly thicker transparent border */
    border-radius: 20px; /* Increased border radius */
    background-color: rgba(255, 255, 255, 0.5); /* Semi-transparent background */
    font-size: 48px; /* Increased font size */
    color: black;
    text-align: center;
    font-family: 'NunitoSans 7pt Condensed Black', sans-serif; /* Apply custom font */
    position: relative; /* Ensure relative positioning for absolute children */
  }
  
  .slogan-text {
    margin-bottom: 20px; /* Adjust the margin to create space */
  }
  