

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: 'Cocogoose', sans-serif;
}

/* Adjust animation durations for bounce effect */
@keyframes bounceTop {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px); /* Adjust bounce height */
  }
}

@keyframes bounceRight {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px); /* Adjust bounce width */
  }
}

@keyframes bounceBottom {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px); /* Adjust bounce height */
  }
}

@keyframes bounceLeft {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-10px); /* Adjust bounce width */
  }
}

/* Apply slower animation to all floating borders */
.floating-border {
  animation-duration: 5s; /* Adjust as needed */
  transition: transform 0.5s ease-out; /* Smooth transition */
}

/* Apply faster animation to AI and IoT */
.floating-border.ai-iot {
  animation-duration: 3s; /* Adjust as needed */
}

/* Hover effect */
.floating-border:hover {
  transform: scale(1.1); /* Example of hover effect */
}

/* App.css */

.app-container {
  width: 100%; /* Set the width to 100% of its container */
  height: 550px; /* Set the height to 100% of the viewport height */
  overflow: hidden; /* Prevent scrollbars */
}

